<template>
  <!-- 设置界面 -->
  <div class="setting">
    <div class="set-head">
      <van-nav-bar title="安全设置" left-text left-arrow @click-left="onClickLeft"></van-nav-bar>
    </div>

    <!-- 功能区 -->
    <div class="set-function">
      <div class="fun">
        <ul>
          <li>
            <div class="fun-fl">
              <span>手机绑定</span>
            </div>
            <div class="fun-fr">
              <span>xxxxxxxxxxxx</span>
              <van-icon name="arrow" color="#999" />
            </div>
          </li>
          <li>
            <div class="fun-fl">
              <span>找回密码</span>
            </div>
            <div class="fun-fr">
              <span></span>
              <van-icon name="arrow" color="#999" />
            </div>
          </li>
          <li>
            <div class="fun-fl">
              <span>修改密码</span>
            </div>
            <div class="fun-fr">
              <span></span>
              <van-icon name="arrow" color="#999" />
            </div>
          </li>
          <li @click="Cutuser()">
            <div class="fun-fl">
              <span>切换用户</span>
            </div>
            <div class="fun-fr">
              <span></span>
              <van-icon name="arrow" color="#999" />
            </div>
          </li>
        </ul>
      </div>
      <div class="btn">
        <van-button
          @click="Quit()"
          type="primary"
          block
          color="linear-gradient(to right, #4bb0ff, #6149f6)"
        >退出登录</van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user:''
    };
  },
  computed: {},
  methods: {
    onClickLeft(){
    this.$router.go(-1); 
    },
    //切换用户
    Cutuser() {      
      if(this.user){
        localStorage.removeItem("user");
        this.$router.push({name:'register'});
        this.user = '';
        return
      }
       this.$toast({
                message: "检测不到登录状态,请去登录"
              });
    },
    //退出登录
    Quit() {  
     if(this.user){
        localStorage.removeItem("user");       
        this.user = '';
        this.$router.push({name:'my'});
          this.$toast({
                message: "退出登录成功"
              });
          return
     }
             this.$toast({
                message: "检测不到登录状态,请去登录"
              });
    },
    //修改密码

    //找回密码

  },
  components: {},
  created() {
    this.user = JSON.parse(localStorage.getItem('user'));
  }
};
</script>
<style lang="less">
.setting {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: white;
  .set-head {
    .van-nav-bar__left {
      width: 50px;
      left: 0;
      .van-icon {
        color: #000;
      }
    }
  }
  .set-function {
    width: 100%;
    height: calc(100% - 46px);
    background: #f5f5f5;
    position: relative;
    .fun {
      width: 100%;
      overflow: hidden;
      margin-top: 10px;
      float: left;
      ul {
        width: 100%;
        overflow: hidden;
        li:active{
          background: rgba(241,243,244,0.1);
        }
        li {
          float: left;
          width: calc(100% - 20px);
          background: white;
          padding: 10px;
          font-size: 15px;
          margin-bottom: 2px;
          .fun-fl {
            float: left;
          }
          .fun-fr {
            float: right;
            span {
              float: left;
              margin-right: 5px;
            }
            .van-icon {
              top: 3px;
              float: right;
            }
          }
        }
        li:nth-child(4) {
          margin-top: 20px;
        }
      }
    }
    .btn {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      overflow: hidden;
    }
  }
}
</style>